@-webkit-keyframes appear{
    0% {opacity: 0}
    100% {opacity: 1}
}
@keyframes appear{
    0% {opacity: 0}
    100% {opacity: 1}
}
@-webkit-keyframes disappear{
    0% { opacity: 1; }
    100% {opacity: 0; }
}
@keyframes disappear{
    0% { opacity: 1; }
    100% {opacity: 0; }
}
@-webkit-keyframes reduce{
    from {
        opacity: 1;
    }
    to {
        opacity: 0.4;
    }
}
@keyframes reduce{
    from {
        opacity: 1;
    }
    to {
        opacity: 0.4;
    }
}
